

body {
  
}

a,
h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  display: block;
}

.is-hidden {
  display: none;
}

.container {
  padding-left: 16px;
  padding-right: 16px;
  @include mq(tablet) {
    width: 320px;
  }
  @include mq(desktop) {
    width: 768px;
  }
}